const arraysAreEqual = (arr1, arr2) => {
    let retVal = true
    if (arr1.length !== arr2.length) {
        retVal = false
    } else {
        arr1.forEach((val, index) => {
            if (arr2[index] !== val) {
                retVal = false
            }
        })
    }
    return retVal
}

