const PromptsPlaceholder = () => {
    return (
        <div>There are no details which need to be recorded for this intervention.</div>
    )
}

const ConfirmInterventionButton = ({clickHandler, enabled}) => {
    return (
        <Button
            size="sm"
            color="primary"
            disabled={!enabled}
            onClick={clickHandler}
            className="confirm-intv"
        >
            Confirm Intervention was performed
        </Button>
    )
}

// const IntvForm = ({ id, label, system = null, prompts = [] }) => {
const IntvForm = ({intvID}) => {
    const {
        scenario,
        // getCheckListIDValues,
        // checkListItems,
        evalIDExpression,
        // setIntv,
        // setCurrIntv,
        setPatientStatusTab,
        setShowIntvSelection,
        setSystemsTab,
        // isChecked,
        // setChecked,
        // setDerivedCheckListItemValue,
        // currentInterventionForm
    } = useScenario()

    const [form, setForm] = useState(null)
    const [intvVariant, setIntvVariant] = useState(null)
    const [distPromptState, setDistPromptState] = useState(null)
    const [promptState, setPromptState] = useState(null)
    const [displayDistPrompts, setDisplayDistPrompts] = useState(false)
    const [displayPrompts, setDisplayPrompts] = useState(false)
    const [displaySubmit, setDisplaySubmit] = useState(false)
    // const [displayedPromptIDs, setDisplayedPromptIDs] = useState([])
    // const [expandedPrompts, setExpandedPrompts] = useState([])

    const submitDistPrompts = (event) => {
        event.preventDefault()
        // FIXME: need to set intvVariant
        setDisplayDistPrompts(false)
        setDisplayPrompts(true)
    }

    const submitPrompts = (event) => {
        event.preventDefault()
        // I'm sure need to do other stuff
        setDisplayPrompts(false)
        setDisplaySubmit(true)
    }

    const isSelected = (id, value, stateVar) => {
        // return whether stored value is same (if string) of if it's a list, whether value is in list
        if (id in stateVar) {
            return (typeof stateVar[id] === "string") ? value === stateVar[id] : stateVar[id].includes(value)
        }
        // this should probably be an error
        return false
    }

    const setSelected = (id, value, checked, stateVar, stateSetter) => {
        // console.log(`setSelected() id: ${id}, value ${value} checked: ${checked}`)
        if (id in stateVar) {
            if (typeof stateVar[id] === "string") {
                if (!checked) {
                    // toggle off value
                    value = ""
                }
                stateSetter(prevState => ({...prevState, [id]: value}))
            } else {
                const currVals = [...stateVar[id]]
                if (checked) {
                    currVals.push(value)
                } else {
                    const idx = currVals.findIndex(pID => pID === value)
                    currVals.splice(idx, 1)
                    stateSetter(prevState => ({...prevState, [id]: currVals}))
                }
            }
        } else {
            console.log(`${id} not found in forms state`)
        }
    }

    const distPromptOptionIsSelected = (id, value) => isSelected(id, value, distPromptState)

    const promptOptionIsSelected = (id, value) => isSelected(id, value, promptState)

    const setDistPromptOptionSelected = (id, value, checked) => setSelected(id, value, checked, distPromptState, setDistPromptState)

    const setPromptOptionSelected = (id, value, checked) => setSelected(id, value, checked, promptState, setPromptState)


    const confirmIntvHandler = (event) => {
        setIntv(id, label, system, true)
        setCurrIntv(null)
        setShowIntvSelection(true)
        if (!!system) {
            setPatientStatusTab("interventionsStatus")
            setSystemsTab(system)
        }
    }

    // const computePrompts2Display = () => {
    //     // const toDisplay = []
    //     return form.prompts.map(e => e.id)
    //     // prompts.forEach(prompt => {
    //     //     if (undefined === prompt.showConstraints) {
    //     //         toDisplay.push(prompt.id)
    //     //     } else if (evalIDExpression(prompt.showConstraints)) {
    //     //         toDisplay.push(prompt.id)
    //     //     }
    //     // })
    //     // return toDisplay
    // }

    const setInitialState = (prompts) => {
        const tmp = {}
        prompts.forEach(p => {
            if (p.selectionType === SELECTION_TYPES.ONE) {
                tmp[p.id] = ""
            } else if (p.selectionType === SELECTION_TYPES.OOM) {
                tmp[p.id] = []
            }
        })
        return tmp
    }
    useEffect(
        () => {
            if (!form) {
                const frm = scenario.getInterventionForm(intvID)
                setForm(frm)
                if (frm.distPrompts.length > 0) {
                    setDisplayDistPrompts(true)
                } else {
                    setDisplayPrompts(true)
                }
                setDistPromptState(setInitialState(frm.distPrompts))
                setPromptState(setInitialState(frm.prompts))
            }
        }, [intvID, form]
    )

    // useEffect(
    //     () => {

    //         const toDisplayIDs = computePrompts2Display()
    //         if (toDisplayIDs !== displayedPromptIDs) {
    //             setDisplayedPromptIDs(toDisplayIDs)
    //             const toDisplay = toDisplayIDs.map(tdID => getPromptAndOptions(scenario, tdID))
    //             setExpandedPrompts(toDisplay)
    //         }
    //     },
    //     [checkListItems, prompts]
    // )

    if (!form) {
        return (<div>processing....</div>)
    }
    return (
        <Panel
            title={form.abbrLabel}
            centerTitle={true}
            collapsable={false}
            scrollBody={true}
        >
            {displayDistPrompts &&
             <>
                <PromptList
                    prompts={form.distPrompts}
                    isSelected={distPromptOptionIsSelected}
                    setSelected={setDistPromptOptionSelected}
                />
                <Button
                    size="sm"
                    color="primary"
                    onClick={submitDistPrompts}>
                    Continue
                </Button>
            </>
            }
            {displayPrompts &&
               <>
               {form.prompts.length > 0
                ?
                    <PromptList
                        prompts={form.prompts}
                        isSelected={promptOptionIsSelected}
                        setSelected={setPromptOptionSelected}
                    />
                : <PromptsPlaceholder />
               }
                <Button
                    size="sm"
                    color="primary"
                    onClick={submitPrompts}
                >
                    Continue
                </Button>
            </ >
            }
            {displaySubmit &&
                <ConfirmInterventionButton
                    clickHandler={confirmIntvHandler}
                    enabled={form.prompts.length === 0}
                />
            }
        </Panel>
    )
    // return (
    //     <Panel
    //         title={label}
    //         centerTitle={true}
    //         collapsable={false}
    //         scrollBody={true}
    //     >
    //         {displayedPromptIDs.length > 0
    //             ? <PromptList
    //                 prompts={ expandedPrompts }
    //               />
    //             : <PromptsPlaceholder />
    //         }
    //         <ConfirmInterventionButton
    //             clickHandler={confirmIntvHandler}
    //             enabled={displayedPromptIDs.length === 0 || getCheckListIDValues(displayedPromptIDs).every(item => !!item)}
    //         />
    //     </Panel>
    // )

}

export default IntvForm