import Prompt from "./Prompt"

const PromptList = ({prompts, isSelected, setSelected}) => {
    return (
        <ul className="prompt-list">
            {prompts.map(prompt => (
                <li key={prompt.id}>
                    <Prompt {...prompt} isSelected={isSelected} setSelected={setSelected} />
                </li>)
            )}
        </ul>
    )
}

export default PromptList