import { SELECTION_TYPES as ST } from "@meddbriefer/scenario-data/constants.js"

// import { useScenario } from "../../contexts/ScenarioContext"

// import CheckmarkIcon from "../icons/CheckmarkIcon"

import CheckBoxGroup from "./CheckBoxGroup"
import RadioGroup    from "./RadioGroup"

const Prompt = ({ id, label, abbrLabel, selectionType, answers, isSelected, setSelected }) => {
    // const { isChecked } = useScenario()
    const getLabel = () => !!abbrLabel ? abbrLabel : label

    const getPrompt = () => {
        switch(selectionType) {
            case ST.ONE:
                return (
                    <RadioGroup
                        id={id}
                        options={answers}
                        isSelected={isSelected}
                        setSelected={setSelected}
                    />
                )
            case ST.OOM:
                return (
                    <CheckBoxGroup
                        id={id}
                        options={answers}
                        isSelected={isSelected}
                        setSelected={setSelected}
                    />
                )
        }
    }
    return (
        <>
            {/* {ST.NONE !== selectionType && <CheckmarkIcon checked={isSelected(id)} />} */}
            <i className="prompt-label">{getLabel()}</i>
            {getPrompt()}
        </>
    )
}

export default Prompt