const CheckBoxGroup = ({ id, options, isSelected, setSelected }) => {
    // const { checkListItems, isChecked, setChecked, someChecked, toggleIntvAnswer } = useContext(ScenarioContext)

    const getLabel = (option) => !!option.abbrLabel ? option.abbrLabel : option.label

    const changeHandler = (event, option) => {
        const {name, value, checked} = event.target
        setSelected(name, value, checked)
        // console.log(option)
        // toggleIntvAnswer(option)
    }

    // useEffect(() => {
    //     if (someChecked(options.map(opt => opt.id))) {
    //         if (!isChecked(id)) {
    //             setChecked(id, true)
    //         }
    //     } else {
    //         if (isChecked(id)) {
    //             setChecked(id, false)
    //         }
    //     }
    // }, [options, setChecked, someChecked])


    return (
        <ul className="checkbox-group-options">
            {options.map(opt => (
                <li
                    key={opt.id}
                    className="prompt-option"
                >
                    <label>
                        <input
                            type="checkbox"
                            className="prompt-checkbox"
                            name={id}
                            value={opt.id}
                            checked={isSelected(id, opt.id)}
                            onChange={(event) => changeHandler(event, opt)}
                        />
                        {getLabel(opt)}
                    </label>
                    {/* <FormCheckBox
                        id={opt.id}
                        label={opt.label}
                        abbrLabel={opt.abbrLabel}
                        clickHandler={(event) => clickHandler(event, opt)}
                    /> */}
                </li>
            ))}
        </ul>
    )
}

export default CheckBoxGroup